.jackpotContainer {
  width: 1200px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
}

.jackpotRow {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 30px 0;
}
.jackpotHeading {
  font-size: 40px;
  padding-left: 20px;
  padding-right: 50px;
  margin-right: 50px;
  border-right: 1px solid #616368;
}
.jackpotParagraph {
  font-size: 16px;
  color: #616368;
}
